import { useAuth } from "../Provider/Auth";
import Login from "./Login";
const Dashboard = () => {
  const { loggedIn, userRole } = useAuth();
  return (
    <>
      <h1>Home Page</h1>
      {loggedIn && <h3>Welcome back {userRole}</h3>}
      {!loggedIn && <Login />}
    </>
  );
};

export default Dashboard;
